.navbar-wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
  font-family: "Raleway", sans-serif;
  color: hsl(0, 0%, 100%);
  background-color: hsl(218, 28%, 13%);
}

.navbar-list {
  display: flex;
  gap: 2rem;
}

.navbar-link,
.navbar-link > a {
  list-style: none;
  text-decoration: none;
  color: hsl(0, 0%, 100%);
}
